<template>
    <div v-if="login_way" class="content">
        <div class="text">管理员登录</div>
        <form style="margin-top: 120px">
            <label >手机号</label>
            <input type="text" class="cell" placeholder="请输入手机号"><br>
            <label for="password">密码</label>
            <input type="text" id="password" placeholder="请输入密码"><br>
            <input type="submit" value="登 录" class="login">
        </form>
        <div class="pass"><a @click="phone_login">密码登录</a></div>
    </div>
    <div v-else class="content">
        <div class="text">管理员登录</div>
        <form style="margin-top: 120px">
            <label >手机号</label>
            <input type="text" class="cell" placeholder="13858000000"><br>
            <label class="auth_label"  for="auth">验证码</label>
            <input type="text" id="auth" placeholder="650512">
            <span class="send_verification" @click="send">{{send_msg}}</span>
            <input type="submit" value="登 录" class="login">
        </form>
        <div class="pass"><a @click="phone_login">验证码登录</a></div>


    </div>
</template>

<script>
  export default {
    name: "admin",
    data(){
      return{
       login_way: true,
        send_msg:"获取验证码"
      }
    },
    methods:{
            send(){
                this.send_msg="已发送"
            },
      phone_login(){
        this.login_way=!this.login_way

      }


    }
  }
</script>

